
body {
  width: 100%;
  height: 100%;
  
}

.container {
  width: 100%;
  height: 100vh;
  margin: 0 auto;
  display: flex; 
  align-items: center;
}

.main{
  width: 90%;
  margin: 0 auto 100px auto;
}

h1 {
  font-size: 50px; 
  background-color: rgb(241, 220, 220);
  text-align: center;
}

th{
  padding: 20px;
  color: #000000;
}

tbody tr{
  height: 30px;
  background-color: rgb(157, 181, 196);
   
}

tbody tr:nth-child(even){
  background-color: rgb(233, 162, 162);
}

thead {
  background-color: rgb(255, 153, 0);
  padding: 20px;
 
  
  
}

input[type=search] {
  float: right;
 margin: 15px;
}

table {
  width: 100%;
  overflow-x:auto;
  margin: 15px;
}